import { Avatar, Badge, Box, Button, Card, CardBody, CardFooter, CardHeader, Flex, Heading, IconButton, Image, SimpleGrid, Spacer, Stack, Text } from "@chakra-ui/react";

import { Container } from "@chakra-ui/react";

function Events() {
    const events = {
        1: {
            title: "Startup Conclave",
            description: "A platform for startups to showcase their products and services, network with investors and mentors, and gain exposure.",
        },
        2: {
            title: "Shark Tank",
            description:
                "A competition where students and teachers will have the opportunity to present their startup ideas to a panel of judges for a chance to win prizes and funding.",
        },
        3: {
            title: "Speaker Session",
            description: "A series of talks by successful entrepreneurs, investors, and thought leaders, sharing their experiences, insights, and advice.",
        },
        4: {
            title: "Networking",
            description: " A chance for students, teachers, entrepreneurs, investors, and mentors to connect, exchange ideas, and build relationships.",
        },
    };
    return (
        <Container maxW='7xl'>
            <Heading mb={5} size={"4xl"}>Events</Heading>
            <SimpleGrid columns={[1, 2, 3]} spacing={8}>
                {Object.keys(events).map((key) => (
                    <Card maxW="md" background="transparent" boxShadow='dark-lg'>
                        <CardHeader>
                            <Flex spacing="4">
                                <Flex flex="1" gap="4" alignItems="center" flexWrap="wrap">
                                    <Badge fontSize="24px" colorScheme="orange">{key}</Badge>
                                    <Box>
                                        <Heading size="lg" color={"orange"}>{events[key].title}</Heading>
                                        <Text as="small">Dates to be announced</Text>
                                    </Box>
                                </Flex>
                            </Flex>
                        </CardHeader>
                        <CardBody>
                            <Text>{events[key].description}</Text>
                        </CardBody>
                    </Card>
                ))}
                ;
            </SimpleGrid>
        </Container>
    );
}

export default Events;
