import { Divider, HStack, Heading, Icon, Link, VStack } from "@chakra-ui/react";
import { Facebook, Instagram, Twitter, Youtube } from "../assets/icons";

function Follow() {
    return ( 
        <VStack mt={10} >
            <Heading>Follow Us</Heading>
            <HStack spacing={8}>
                <Link href="https://www.facebook.com/iicgalgotiasuniversity/">
                    <Icon as={Facebook} w={12} h={12}/>
                </Link>
                <Link href="https://www.youtube.com/@iicgalgotiasuniversity1670">
                    <Icon as={Youtube} w={12} h={12}/>
                </Link>
                <Link href="https://www.instagram.com/iic.galgotiasuniversity/?hl=en">
                    <Icon as={Instagram} w={12} h={12}/>
                </Link>
                <Link href="https://mobile.twitter.com/iicgalgotiasuni">
                    <Icon as={Twitter} w={12} h={12}/>
                </Link>
            </HStack>
        </VStack>
     );
}

export default Follow;